  --primary-pink: #ff8787;
  --secondary-pink: #ffc9c9;
  --tertiary-pink: #fa5252;
  --primary-green: #12b886;
  --secondary-green: #099268;
  --tertiary-green: #38d9a9;
}

* {
  font-family: "Oswald", sans-serif;
  font-family: "Roboto Condensed", sans-serif;
  box-sizing: border-box;
}

html,
body {
  margin: 0;
}

body {
  height: 100vh;
  display: grid;
  grid:
    "header" 1.5fr
    "main" 8.5fr;
}

header {
  grid-area: header;
  z-index: 1;
  display: grid;
  grid-template-columns: 2.5fr 7.5fr;
  border-bottom: 4px solid black;
  box-shadow: 0px 0px 4px black;
}

main {
  grid-area: main;
  background-color: var(--secondary-pink);
}

.menu {
  z-index: 1;
  width: 0;
  min-width: 0;
  background-color: var(--primary-pink);
  height: 100%;
  border-right: 0px solid black;
  transition:
    width 0.5s ease-in-out,
    min-width 0.5s ease-in-out,
    border-right 0s 0.5s ease-in-out;
}

.menu.active {
  width: 25%;
  min-width: 250px;
  border-right: 4px solid black;
  box-shadow: 0px 0px 4px black;
  transition:
    width 0.5s ease-in-out,
    min-width 0.5s ease-in-out;
}

.menu-container {
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 2rem;
  width: 0;
  height: 0;
  opacity: 0;
  overflow: hidden;
  cursor: pointer;
}

.menu-container.active {
  padding: 1.5rem 0 0 2rem;
  width: 100%;
  height: 100%;
  opacity: 1;
}

.menu-item-header {
  position: relative;
  margin: 0;
  opacity: 0;
  transition:
    opacity 0.2s 0.5s ease-in-out,
    font-size 0.2s 0.5s ease-in-out,
    color 0.7s ease-in-out;
}

.menu-item-header.active {
  font-weight: 700;
  font-size: 1.5rem;
  opacity: 1;
  transition:
    color 0.1s 0.5s ease-in-out,
    opacity 0.2s 0.5s ease-in-out,
    font-size 0.2s 0.5s ease-in-out;
}

.menu-item-header.active.selected {
  transition: color 0.5s ease-in-out;
  color: white;
}

.menu-item-header:hover {
  transition: color 0.1s 0.2s ease-in-out;
  color: var(--primary-green);
}

.menu-item-header::after {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 3px;
  width: 0;
  transition:
    width 0.7s ease-in-out,
    background-color 0s 0.7s ease-in-out;
  display: inline-block;
  content: "";
}

.menu-item-header:hover::after {
  transition: width 0.2s ease-in-out;
  width: 100%;
  background-color: var(--primary-green);
}

.menu-item-header.active.selected::after {
  transition:
    width 0.3s ease-in-out,
    background-color 0.3s 0.3s ease-in-out;
  width: 100%;
  background-color: white;
}

.submenu {
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 0.5rem;
  opacity: 0;
  height: 0;
  width: 0;
}

.submenu.active {
  transition: opacity 0.5s ease-in-out;
  opacity: 1;
  height: auto;
  width: 100%;
  margin-top: 1rem;
  margin-left: 0.5rem;
}

.submenu-item-header {
  margin: 0;
  height: 0;
  width: 0;
}

.submenu-item-header.active {
  transition: opacity 0.5s 0.2s ease-in-out;
  height: auto;
  width: 100%;
}

.menu-button {
  place-self: center left;
  margin-left: 2rem;
  cursor: pointer;
  display: grid;
}

.logo {
  place-self: center left;
  margin-left: 2rem;
}

.logo h3 {
  font-family: "Oswald", sans-serif;
  margin: 0;
  font-weight: 700;
  font-size: 2rem;
  color: var(--tertiary-pink);
}

.icon-container {
  position: relative;
  place-self: center;
  height: 60px;
  width: 60px;
  border: 4px solid var(--secondary-green);
  border-radius: 8px;
  transition: all 0.5s ease-in-out;
}

.icon-container.active {
  border-color: transparent;
}

.icon-container:hover {
  background-color: rgba(130, 130, 130, 0.125);
}

.icon-part {
  position: absolute;
  display: inline-block;
  height: 4px;
  width: 46px;
  background-color: var(--primary-green);
  transition: all 0.5s ease-in-out;
  border-radius: 4px;
}

.icon-part.part-1 {
  transform-origin: center;
  transform: translate(calc((52px - 46px) / 2), calc((52px - 4px) / 4 * 1));
}

.icon-part.part-1.active {
  transform: translate(3px, 25px) rotate(45deg);
}

.icon-part.part-2 {
  transform-origin: center;
  transform: translate(calc((52px - 46px) / 2), calc((52px - 4px) / 4 * 2));
  transition: all 0.25s 0.25s ease-out;
}

.icon-part.part-2.active {
  transition: all 0.25s;
  background-color: transparent;
}

.icon-part.part-3 {
  transform-origin: center;
  transform: translate(calc((52px - 46px) / 2), calc((52px - 4px) / 4 * 3));
}

.icon-part.part-3.active {
  transform: translate(3px, 25px) rotate(-45deg);
}
